.wrapper{
   display: flex;
   width: 100%;
}
.sidebar{
  position: relative;
}
.bg_shadow{
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: #000;
  opacity: 0;
  transition: all 0.3s ease;
  visibility: hidden;
  z-index: 998;
}
.sidebar_inner{
    width: 200px;
    position: fixed;
    top: 0;
    left: 0;
    background: #2eb2f0;
    height: 100vh;
    z-index: 999;
    transition: all 0.3s ease;

}

.profile_info{
    padding: 20px;
    text-align: center;
}
.profile_img{
  width: 100px;
  margin: 0 auto 10px;
}
.profile_img img{
  width: 100%;
  display: block;
  border-radius: 50%;
}
.profile_data .name{
      font-size: 18px;
      color: #fff;
      margin-bottom: 5px;
      text-transform: capitalize;
}
.profile_data span{
  color: #c4dcff;
}

.sidebar_inner .siderbar_menu{
  height: 550px;
  overflow: auto;

}
.sidebar_inner .siderbar_menu > li > a{
    padding: 12px 20px;
    color: #c4dcff;
    text-decoration: none;
    display: flex;
    align-items: center;
    position: relative;
    margin-bottom: 2px;

}

.sidebar_inner .siderbar_menu > li > a .icon{
  margin-right: 15px;
  font-size: 25px;
}
.sidebar_inner .siderbar_menu > li > a:hover,
.sidebar_inner .siderbar_menu > li.active > a{
  background: #0c6a96;
  color: #fff;
}
.sidebar_inner .siderbar_menu > li > a .arrow{
  position: absolute;
  top: 20px;
  right: 10px;
}
.sidebar_inner .siderbar_menu > li > a .title{
  font-size: 18px;

}
.sidebar_inner .close{
  position: absolute;
  top: 5px;
  right: 15px;
  color: #fff;
  font-size: 25px;
  cursor: pointer;
  display: none;
}
.sidebar_inner .close:hover,
.navbar .hamburger .fas:hover{
  opacity: 0.7;
}

.main-container{
  margin-left: 200px;
  width: calc(100% - 200px);
  transition: all 0.3s ease;
}

.navbar{
    width: calc(100% - 200px);
    display: flex;
    justify-content: space-between;
    height: 50px;
    padding: 15px 25px;
    background:#8dc9f7;
    position: fixed;
    top: 0;
    left: 200px;
    box-shadow: 0 2px 2px rgba(0,0,0, 0.125);
    transition: all 0.3s ease;
}
.navbar .hamburger{
  width: 30px;
  height: 30px;
  position: relative;
  display: none;
  
}
.navbar .hamburger .fas{
  position: absolute;
  font-size: 25px;
  color: #222;
  cursor: pointer;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.navbar .menu{
  display: flex;
  text-align: center;
}
 .navbar .menu li a{
  margin: 0 15px;
  padding: 0 15px;
  position: relative;
  color: #f1f1f1;
  font-weight: 700;
  text-align: center;
  text-transform: capitalize;
  text-decoration: none;
}

 .navbar .menu li a::before{
  content: "";
  position: absolute;
  bottom: -20px;
  left: 0;
  width: 100%;
  height: 3px;
  background: #2eb2f0;
  display: none;
}

 .navbar .menu li a.active,
.navbar .menu li a:hover
{
    color:#222;
}
.navbar .menu li a.active::before,
 .navbar .menu li a:hover::before{
    display: block;

}
 .navbar .right_menu ul li{
    position: relative;
}
 .navbar .right_menu ul li .fas{
    font-size: 22px;
    cursor: pointer;
    color: #525353;
}
.navbar .right_menu ul li .fas:hover{
     color: #2eb2f0;
}

.navbar .right_menu ul li .profile_dd{
  position: absolute;
  top: 35px;
  right: -10px;
  width: 180px;
  padding: 10px 0;
  background: #fff;
  box-shadow: 0 0 2px rgba(0,0,0, 0.5);
  border-radius: 3px;
  user-select:none;
  display: none;
  z-index: 2;
}
.circle{
  width: 50px;
  height: 50px;
  text-align: center;
  border: 1px solid #0c6a96;
  border-radius: 50%;
  padding: 4px;
}
.circle:hover{
  border-color: #000;
}
 .navbar .right_menu ul li .profile_dd .dd_item{
 padding: 10px;
 cursor: pointer;
 cursor:#525353;
 
 
}
 .navbar .right_menu ul li .profile_dd .dd_item:hover{
  background-color: rgba(0,0,0,0.4);

}
 .navbar .right_menu ul li .profile_dd:before{
  content: "";
  position: absolute;
  top: -20px;
  right: 10px;
  border: 10px solid;
  border-color: transparent transparent #fff transparent;
}
 .navbar .right_menu ul li .profile_dd.active{
  display: block;
}


.accordion{
  padding-left: 50px;
  transition: all 0.3s ease;
  height: 0;
  overflow: hidden;
}
.accordion li a{
  display: block;
  color: #ecf1f8;
  padding: 12px 0;
  padding-left: 10px;
  text-decoration: none;
}
.accordion li a:hover,
.accordion li a.active{
  color: #fff;
  background: #0c6a96;

}

.wrapper.active .sidebar_inner{
  left: 0;
}
.wrapper.active .bg_shadow{
  visibility: visible;
  opacity: 0.7;
}

.siderbar_menu > li.active .accordion{
   height: 200px;
}
.siderbar_menu > li.active .arrow{
  transform: rotate(180deg);
  transition: all 0.3s ease;
}


/*main content style */
.content{
  margin-top: 60px;
  display: grid;
  grid-gap:20px;
  width: calc(100% - 200px);

}
.top-container{
  display: grid;
  grid-gap:20px;
  grid-template-areas: "grapharea grapharea top-box-a"
                       "grapharea grapharea top-box-b";
}

 .grapharea{
   grid-area: grapharea;
   min-height: 400px;
   width: 100%;
   background: #fff;
   padding: 1rem;
   box-shadow: 0 1px 5px rgba(104,104,104,0.8);
   margin-left: 10px;
   
}

.top_box_a{
  grid-area: top-box-a;
  background: #fff; 
  height: 200px;
  margin-right: 4px;
  box-shadow: 0 1px 5px rgba(104,104,104,0.8);
}
.top_box_b{
  grid-area: top-box-b;
  background: #fff;
  margin-right: 4px;
  height: 200px;
  box-shadow: 0 1px 5px rgba(104,104,104,0.8);
}

.chart-title{
    padding: 5px;
    text-align: center;
    border-bottom: 1px solid rgb(121, 118, 118);
    width: 100%;
    
}
.chart-title h4{
  font-size: 20px;
  font-family: 'Anton', sans-serif;
  font-weight: 700;
  color: #2eb2f0;
}
table {
  border-collapse: collapse;
  width: 100%;
  text-align: center;
}

th, td {
  padding: 8px;
  text-align: left;
  border-bottom: 1px solid #ddd;
  color: #525353;
}

tr:hover {background-color:#f5f5f5;}

/* end of main content style  */
.lg-table{
  position: relative;
  width: 100%;
}
.cust-table{
  width: 100%;
  margin: 5px auto;
  background: #fff;
  box-shadow: 0 1px 5px rgba(104,104,104,0.8);
  text-align: center;
  border-collapse: collapse;
  border-spacing: 0;
  padding: 10px 15px;
}
.cust-table th{
  background: #0c6a96;
  color: #f1f1f1;
}
.cust-table th td{
  padding: 8px 25px;
  text-align: center;
}
.btn2{
  padding: 5px 10px;
  background: #8dc9f7;
  color: #f1f1f1;
  border-radius: 5px;
  text-align: center;
  outline: none;
  border: none;
  width: 150px;
  display: block;
  font-size: 15px;
}
.btn{
  padding: 5px 10px;
  background: #07bb52;
  color: #f1f1f1;
  border-radius: 5px;
  text-align: center;
  outline: none;
  border: none;
  width: 150px;
  display: block;
  font-size: 15px;
}
.btn1{
  padding: 8px 15px;
  background: #b1c311;
  color: #f1f1f1;
  border-radius: 5px;
  text-align: center;
  outline: none;
  border: none;
  width: 150px;
  display: block;
  font-size: 15px;
}

@media (max-width:1024px){
     .sidebar_inner{
       left: -250px;
       transition: all 0.3s ease;
     }
     .main-container{
       width: 100%;
       margin-left: 0;
     }
     .navbar{
       width: 100%;
       left: 0;
     }
     .sidebar_inner .close,
     .navbar .hamburger{
       display: block;
     }
}
@media (max-width:900px){
   .top-container{
     grid-template-areas: 'grapharea grapharea'
                           'top-box-a top-box-b';
   }
}

@media (max-width:528px){
  .navbar .menu{
    display:none;
  }
}